import React from "react";
import './footer.scss';
import regumlogo from '../../assets/images/regumlogo.png';


function Footer() {



  function HeadItem(props) {
    return (
      <p className="headItem">{props.title}</p>
    );
  }


  function FooterItem(props) {
    return (


      <p className="footerItem">
        <a href={props.href}><span className="footerItemContent">{props.title}</span></a>
      </p>

    );
  }



  // function FollowusItem(props) {
  //   return (
  //     <a target="_blank">

  //       <img


  //       ></img>
  //     </a>
  //   );
  // }

  return (
    <>






      <div className="footerContainer">
        <div className="footerSectionsContainer">

          <div className="firstSection">
            <img src={regumlogo} alt='logo'></img>

          </div>

          <div className="secondSection">
            <HeadItem title="Navigation" />

            <div className="secondSectionContainer">

              <div>
                <FooterItem  title="About" />
                <FooterItem  title="Why us" />
                <FooterItem  title="Services" />
                <FooterItem  title="Portofolio" />
                <FooterItem  title="Testimonials" />

              </div>

              <div>
                <FooterItem  title="Blogs" />
                <FooterItem  title="Team" />
                <FooterItem  title="Contact" />

              </div>



            </div>

          </div>





          <div className="thirdSection">
            <div>
              <HeadItem title="Social" />
            </div>
            <div className="thirdSectionItemContainer">
              <FooterItem href=''  title="Facebook" />
              <FooterItem href=''  title="Linkedin" />
              <FooterItem href=''  title="Instgram" />

            </div>
          </div>

          <div className="forthSection">
            <div>
              <HeadItem title="Contact" />
            </div>

            <div className="forthSectionItemContainer">
              <a href="tel:01225003306"><FooterItem  title="0152-1556-688" /></a>
              <FooterItem  title="regumapps@gmail.com" />


            </div>
          </div>
        </div>
        <div className="copyRight">
          <p>REGUMAPPS | All rights reserved &copy;2022 </p>
        </div>
      </div>
    </>
  );
}


export default Footer;
