import "./index.scss"
import $ from "jquery";
import { useEffect ,useState } from "react";
import { display } from "@mui/system";





const Portofolio = () => {

    let datas="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."
    const data = [{img:"https://lh5.googleusercontent.com/MysYcAxi-3E_HjqYR2yyBo2bpN7rmx59X1MWspwE95Hu6U82QzqYGu9J9k2z9nMa7ATZtXtbkTlbXqQOpKCOjDKIna6uoMmtFzFXSRFXL848FX-SR9DFPvFMbvpioqh_Mu39miAM" ,index:0, title:"BFI realstate" ,data:datas},
                {img:"https://static.wixstatic.com/media/3edabd_07ccb7f93745400bbf5646734285ffc2~mv2.jpg/v1/fill/w_924,h_580,al_c,q_90/3edabd_07ccb7f93745400bbf5646734285ffc2~mv2.jpg" ,index:1, title:"Beavers" ,data:datas},
                {img:"//bit.ly/VTv2Wg" ,index:3, title:"BFI realstate" ,data:datas},
                {img:"//bit.ly/VTv2Wg" ,index:4, title:"BFI realstate" ,data:datas},
                {img:"//bit.ly/VTv2Wg" ,index:5, title:"BFI realstate" ,data:datas},
                {img:"//bit.ly/VTv2Wg" ,index:6, title:"BFI realstate" ,data:datas},
                {img:"//bit.ly/VTv2Wg" ,index:7, title:"BFI realstate" ,data:datas}];

    const [webData,setWebData]=useState({ title: data[0].title, data:data[0].data });


                useEffect(() => {
                    const windowWidth=window.innerWidth;
                $("ul li:first-child").addClass("active");
                let space= 0;
                if(windowWidth <= 840 && windowWidth > 416){
                    space= 350;
                }else if(windowWidth <= 415 && windowWidth > 412){
                    space= 450;
                }else if(windowWidth > 840){
                    space= 756;
                }else{
                    space = 240;
                }

                $("li").on("click", function(){
                    var item = $(this),
                        pos = "-"+(item.index() * space)+"px";
                    item.addClass("active");
                    item.siblings().removeClass("active");
                    $("ul").css("left", pos);
                });
                },[]);


                useEffect(() => {
                        $(".info").removeClass("animation");
                        setTimeout(() => {
                            $(".info").addClass("animation");
                        }, );     
                },[webData]);




                const imgs= data?.map((itm)=>{

                    return (
                        <li key={itm.index} onClick={()=>setWebData({title:itm.title,data:itm.data})}>
                            <div className="containImg">
                                <img src={itm.img}/>
                            </div>
                            
                        </li>
                    )
                }) 


    return ( 
<div className="portofolio">


    {/* Component title */}
    <div className="componentTitle">
    <h1><span>Porto</span>folio</h1>
<hr />
    </div>
{/* --------------------- */}

<div className="hide">
<div className="slider">
  <ul>
  {imgs}
  </ul>

</div>
</div>
<div className="info">
    <h2 className="title">{webData.title}</h2> 
    <p  className="data" >{webData.data} <a href="3">View Live</a> </p>
</div>

  {/* buttom Line */}
<hr className="buttomLine"/>
</div>
    );
}
 
export default Portofolio;