import React from "react";
import "./contactUs.scss";
import swal from "sweetalert";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import axios from 'axios'





const SignupSchema = Yup.object().shape({
    name: Yup.string()
        .matches("^[a-zA-Z , '']+$", "Invalid name")
        .min(2, "Too Short!")
        .max(50, "Too Long!")
        .required("Required"),

    email: Yup.string().email("Invalid email").required("Required"),

    phone: Yup.string()
        .matches("^[0-9]+$", "Invalid number")
        .required("Required"),

    company_name: Yup.string(),

    message: Yup.string().min(5, "Too Short").required("Required"),
});

function ContactUsForm() {


    const sendMessageSucsess = () =>
    swal( "Good job!" , "Thank You For Sending Us!", "success");
    const sendMessageError = () =>
    swal("Sending Error", "Please Try Again .", "error");


    return (
        <>
            <div
                className="formContainer"
                id="contact-us"
            >
                <div className="form-secondContainer">
                   

                    <Formik
                        className="form"
                        enableReinitialize
                        initialValues={{
                            name: "",

                            email: "",

                            phone: "",

                            company_name: "",

                            message: "",
                        }}
                        validationSchema={SignupSchema}
                        onSubmit={(values, { resetForm }) => {

                            // same shape as initial values

                            let contactData = {
                                name: values.name,
                                phone: values.phone,
                                email: values.email,
                                company_name: values.company_name,
                                message: values.message,
                            };
                            axios
                                .post('http://127.0.0.1:8000/api/contactUs', contactData)
                                .then((response) => {
                                    if (response.data === 'success') {
                                        sendMessageSucsess()
                                        let inpts = document.querySelectorAll('#name, #phone , #email , #company_name , #msg');
                                        inpts.forEach(input => {
                                            input.value = '';
                                        });
                                        resetForm();

                                    } else {
                                        sendMessageError()
                                    }

                                }).catch((error) => {
                                    sendMessageError()
                                    console.error(error);
                                });
                        }}
                    >
                        {({ errors, touched }) => (
                            <Form>
                                <div className="h-auto">
                                    <div className="input-errMsg-container">
                                        <div className="inputHolder">
                                            <Field
                                                id="name"
                                                name="name"
                                                placeholder="Full Name"
                                                className="contactUs-inputStyle"
                                            />
                                            {errors.name && touched.name ? (
                                                <div className="reqiredWarnMsg">
                                                    {errors.name}
                                                </div>
                                            ) : null}
                                        </div>


                                    </div>

                                    <div className="input-errMsg-container">
                                        <div className="inputHolder input-1-2">
                                            <Field
                                                id="email"
                                                name="email"
                                                placeholder="Email"
                                                className="contactUs-inputStyle"
                                            />
                                            {errors.email && touched.email ? (
                                                <div className="reqiredWarnMsg">
                                                    {errors.email}
                                                </div>
                                            ) : null}
                                        </div>

                                        <div className="inputHolder input-1-2 ">
                                            <Field
                                                id="phone"
                                                name="phone"
                                                placeholder="Phone"
                                                className="contactUs-inputStyle"
                                            />
                                            {errors.phone && touched.phone ? (
                                                <div className="reqiredWarnMsg">
                                                    {errors.phone}
                                                </div>
                                            ) : null}
                                        </div>


                                    </div>
                                    <div className="input-errMsg-container">
                                        <div className="inputHolder">
                                            <Field
                                                id="company_name"
                                                name="company_name"
                                                placeholder="Company name"
                                                className="contactUs-inputStyle"
                                            />
                                            {errors.company_name && touched.company_name ? (
                                                <div className="reqiredWarnMsg">
                                                    {errors.company_name}
                                                </div>
                                            ) : null}
                                        </div>
                                    </div>

                                    <div className="text-area-container">
                                        {" "}
                                        <Field
                                            id="message"
                                            name="message"
                                            as="textarea"
                                            placeholder="Tell us about the project"
                                            className="text-area"
                                        />
                                        {errors.message && touched.message ? (
                                            <div className="reqiredWarnMsg">
                                                {errors.message}
                                            </div>
                                        ) : null}{" "}
                                    </div>


                                    <button
                                    type="submit"
                                    className="submitbtn"

                                >
                                    Submit
                                </button>
                                </div>

                               
                            </Form>
                        )}
                    </Formik>
                </div>
            </div>
        </>
    );
}

export default ContactUsForm;