import Carousel from "react-spring-3d-carousel";
import './slider.scss';
import React, { useState, useEffect } from 'react';
import { useSelector } from "react-redux";
import ReactLoading from "react-loading";

const config = require('react-spring');
const { v4: uuidv4 } = require('uuid');


export default function Slider() {







  const dataApiPromise = useSelector((state) => state.apiReducer);


  const [dataApi, setDataApi] = useState([]);



  useEffect(() => {

    dataApiPromise.then(function (result) {
      setDataApi(result);
      document.querySelector('.regumTeamName').textContent = result[0].name ;
      document.querySelector('.regumTeamPosition').textContent = result[0].position ;

    });



  },[dataApiPromise]);



  const [state, setState] = useState({
    goToSlide: 0,
    offsetRadius: 2,
    showNavigation: true,
    config: config.gentle
  });



  const passthedata = (evt) => {

    let regumTeamName = document.querySelector('.regumTeamName')
    let regumTeamPosition = document.querySelector('.regumTeamPosition')



    let result = slidess.find(item => item.id == evt.target.id);

    regumTeamName.textContent = result.name
    regumTeamPosition.textContent = result.position


  };





  //  ********* Loading *************
  let color = "#45b6ca"
  let type = "spinningBubbles"

  while (!dataApi[0]) {

    return (
      <div className="loadingIcon">

        <ReactLoading type={type} color={color} height={100} width={100} />

      </div>


    );
  }

  const slidess = [];
  dataApi.forEach(element => {
    if (element.image != null) {
      let slideOjects = Object.create(element)

      slideOjects.key = uuidv4();
      slideOjects.content = <img src={"http://127.0.0.1:8000/storage/images/" + element.image} alt={element.id} id={element.id} />

      slidess.push(slideOjects)
    }


  });

  // console.log(slidess)





  // let  slides = [
  //   {
  //     key: dataApi[0].id,
  //     content: <img src={"http://127.0.0.1:8000/storage/images/"+dataApi[1].image} alt="1" id={dataApi[0].id} />,
  //     name: dataApi[0].name,
  //     id : dataApi[0].id ,
  //     position : dataApi[0].position
  //   },
  //   {
  //     key: dataApi[1].id,
  //     content: <img src={"http://127.0.0.1:8000/storage/images/"+dataApi[1].image} alt="2" id={dataApi[1].id} />,
  //     name: dataApi[1].name,
  //     id : dataApi[1].id,
  //     position : dataApi[1].position
  //   },
  //   {
  //     key: dataApi[2].id,
  //     content: <img src={"http://127.0.0.1:8000/storage/images/"+dataApi[1].image} alt="3" id={dataApi[2].id}/>,
  //     name: dataApi[2].name,
  //     id : dataApi[2].id ,
  //     position : dataApi[2].position
  //   },
  //   // {
  //   //   key: uuidv4(),
  //   //   content: <img src={teamPhoto} alt="4" id='4' />,
  //   //   name: 'aly ',
  //   //   id : 4 ,
  //   //   position : 'CEO Regumapps'
  //   // },
  //   // {
  //   //   key: uuidv4(),
  //   //   content: <img src={teamPhoto} alt="4" />
  //   // }

  // ]
  let slides = slidess.map((slide, index) => {
    return { ...slide, onClick: () => setState({ goToSlide: index }) };
  });



  let xDown = null;
  let yDown = null;

  const getTouches = (evt) => {
    return (
      evt.touches || evt.originalEvent.touches // browser API
    ); // jQuery
  };

  const handleTouchStart = (evt) => {
    const firstTouch = getTouches(evt)[0];
    xDown = firstTouch.clientX;
    yDown = firstTouch.clientY;
  };

  const handleTouchMove = (evt) => {
    if (!xDown || !yDown) {
      return;
    }

    let xUp = evt.touches[0].clientX;
    let yUp = evt.touches[0].clientY;

    let xDiff = xDown - xUp;
    let yDiff = yDown - yUp;

    if (Math.abs(xDiff) > Math.abs(yDiff)) {
      /*most significant*/
      if (xDiff > 0) {
        /* left swipe */
        setState({ goToSlide: state.goToSlide + 1 });
      } else {
        /* right swipe */
        setState({ goToSlide: state.goToSlide - 1 });
      }
    } else {
      if (yDiff > 0) {
        /* up swipe */
      } else {
        /* down swipe */
      }
    }
    /* reset values */
    xDown = null;
    yDown = null;
  };




  return (
    <div
      className='carouslContainer'
      style={{ width: "50%", height: "90%", margin: "0px 0px 0px 150px" }}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onClick={passthedata}

    >
      <Carousel
        slides={slides}
        goToSlide={state.goToSlide}
        offsetRadius={state.offsetRadius}
        showNavigation={state.showNavigation}
        animationConfig={state.config}
      />

    </div>
  );
};
