import "./index.scss"
import Logo from "../../assets/images/Regum logo/Regumapp-1.png"
import { useState ,useEffect } from "react"
import Hamburger from "./hamburger";


const Navbar = () => {

const [list,setList]=useState(true);
const [sideList,setSideList]=useState(false);


function NavLinks(){
    if(list){
        return(
            <>
              <div className="grid">Why us</div>
              <div className="grid">Services</div>
              <div className="grid">Portofolio</div>
              <div className="grid">Testimonials</div>
              <div className="grid">Blogs</div>
              <div className="grid">Team</div> 
            </>
        )
    }else if(sideList){
        return(    
            <ul className="side-list">
            <li>About us</li>
            <li>Contact us</li>
            <li>Why us</li>
            <li>Services</li>
            <li>Portofolio</li>
            <li>Testimonials</li>
            <li>Blogs</li>
            <li>Team</li> 
            </ul>
        )
   
    }

}
useEffect(() => {
    const windowWidth=  document.documentElement.clientWidth;
    if(windowWidth <= 980){
        setList(false)
        setSideList(true)
    }else {
        setList(true)
        setSideList(false)
    }
},[]);



    return ( <>
    <nav> 
        
        <section className="upperNav"> 
            <div className="dataContact">
                <ul>
                    <li>regumapps.gmail.com</li>
                    <li>0152-1556-688</li>
                </ul>
            </div>
            <div className="rigtUpper snav">
                <ul className="right">
                    {!sideList && <> <li>About us</li>
                    <li>Contact us</li> </>}
                </ul>
                {sideList && <NavLinks/>}
            </div>
            <Hamburger/>
        </section> 
        <section className="downNav ">
            
            <div className="grid"><img className="logo" src={Logo} alt="logo" /></div>
            {list &&  <NavLinks/> }
        </section>
    </nav>  
    </>);
}
 
export default Navbar;