import React from "react";
import "./RegumTeam.scss";
import Slider from "../slider/Slider";

// import { Link } from "react-router-dom";





function RegumTeam() {






  return (
    <>


      <div className="portofolio regumTeamContainer">
        <div className="regumTeamTitle componentTitle">
            <h1>Regum <span>Team</span></h1>
            <hr></hr>
        </div>

        <div className="secondSection">
            <div className="regumTeamPhotos">
                <Slider/>
            </div>
            <div className="regumteamdataContainer">
              <div className="regumTeamDataHolder">
               <div className="regumTeamDatasecondHolder">
                <div  className="regumTeamData">
                    <p className="regumTeamName"></p>
                    <p className="regumTeamPosition"></p>
                    </div>
                </div>
              </div>
                
            </div>

        </div>


      </div>


    </>
  );
}


export default RegumTeam;