import "./index.scss"
import $ from "jquery";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

const Hero = () => {
    $(function () {
        'use strict';   
        $('.hero').width($(window).width());
        $(window).resize(function() {
        $('.hero').width($(window).width());   
        })
    });

    const particlesInit = async (main) => {
        await loadFull(main);
      }
    return ( <>


    
    <div className="hero">

    
    
        <div className="hero-img">
            <div className=" ddd"> 
            <Particles
    id="tsparticles"
    init={particlesInit}
    options={{
        "fullScreen": {
            "enable": false,
            "zIndex": 1
        },
        "particles": {
       
            "number": {
                "value": 20,
                "density": {
                    "enable": false,
                    "value_area": 800
                }
            },
            "color": {
                "value": "#fff"
            },
            "shape": {
                "type": "circle",
                "color": "#F5024D",
                "options": {
                    "sides": 5
                }
            },
            "opacity": {
                "value": 0.8,
                "random": false,
                "anim": {
                    "enable": false,
                    "speed": 1,
                    "opacity_min": 0.1,
                    "sync": false
                }
            },
            "size": {
                "value": 4,
                "random": false,
                "anim": {
                    "enable": false,
                    "speed": 40,
                    "size_min": 0.1,
                    "sync": false
                }
            },
            "rotate": {
                "value": 0,
                "random": true,
                "direction": "clockwise",
                "animation": {
                    "enable": true,
                    "speed": 5,
                    "sync": false
                }
            },
            "line_linked": {
                "enable": true,
                "distance": 600,
                "color": "#ffffff",
                "opacity": 0.4,
                "width": 2
            },
            "move": {
                "enable": true,
                "speed": 2,
                "direction": "none",
                "random": false,
                "straight": false,
                "out_mode": "out",
                "attract": {
                    "enable": false,
                    "rotateX": 600,
                    "rotateY": 1200
                }
            }
        },
        "interactivity": {
            "events": {
                "onhover": {
                    "enable": true,
                    "mode": ["grab"]
                },
                "onclick": {
                    "enable": false,
                    "mode": "bubble"
                },
                "resize": true
            },
            "modes": {
                "grab": {
                    "distance": 400,
                    "line_linked": {
                        "opacity": 1
                    }
                },
                "bubble": {
                    "distance": 400,
                    "size": 40,
                    "duration": 2,
                    "opacity": 8,
                    "speed": 3
                },
                "repulse": {
                    "distance": 200
                },
                "push": {
                    "particles_nb": 4
                },
                "remove": {
                    "particles_nb": 2
                }
            }
        },
        "retina_detect": true,
        "background": {
           
            
            "position": "50% 50%",
            "repeat": "no-repeat",
            "size": "cover"
        }
    }} />
            </div>
        
            <div className="hero-title">

<div >
                <div className="left-bracket">
                            <svg  viewBox="0 0 327 601" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M218.32 600.497H191.367C179.453 600.497 168.418 598.349 158.262 594.052C148.301 589.95 139.707 584.286 132.48 577.06C125.449 569.833 119.883 561.142 115.781 550.985C111.875 540.829 109.922 529.696 109.922 517.587V393.368C109.922 360.556 91.6602 344.149 55.1367 344.149V311.337C91.6602 311.337 109.922 294.931 109.922 262.118V136.142C109.922 124.032 111.875 112.997 115.781 103.036C119.883 93.0752 125.449 84.4814 132.48 77.2549C139.707 70.0283 148.301 64.4619 158.262 60.5557C168.418 56.4541 179.453 54.4033 191.367 54.4033H218.32V86.9229H191.367C158.164 86.9229 141.562 103.329 141.562 136.142V262.118C141.562 268.759 140.293 275.692 137.754 282.919C135.215 290.146 131.699 296.884 127.207 303.134C122.91 309.384 117.637 314.853 111.387 319.54C105.137 324.032 98.3984 326.864 91.1719 328.036C98.3984 329.208 105.039 332.04 111.094 336.532C117.148 340.829 122.422 346.103 126.914 352.353C131.602 358.603 135.215 365.341 137.754 372.567C140.293 379.599 141.562 386.532 141.562 393.368V517.587C141.562 550.79 158.164 567.392 191.367 567.392H218.32V600.497Z" fill="#F5024D" fill-opacity="0.5"/>
                </svg>
                            </div>



                <div className=" title">
                <h1>welcome</h1>
                                <h1>to</h1>
                                <span>regumapps</span>
                </div>
                
                            <div className="right-bracket">
                            <svg  viewBox="0 0 327 601" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M108.686 600.497H135.639C147.553 600.497 158.588 598.349 168.745 594.052C178.706 589.95 187.299 584.286 194.526 577.06C201.557 569.833 207.124 561.142 211.225 550.985C215.131 540.829 217.084 529.696 217.084 517.587V393.368C217.084 360.556 235.346 344.149 271.87 344.149V311.337C235.346 311.337 217.084 294.931 217.084 262.118V136.142C217.084 124.032 215.131 112.997 211.225 103.036C207.124 93.0752 201.557 84.4814 194.526 77.2549C187.299 70.0283 178.706 64.4619 168.745 60.5557C158.588 56.4541 147.553 54.4033 135.639 54.4033H108.686V86.9229H135.639C168.842 86.9229 185.444 103.329 185.444 136.142V262.118C185.444 268.759 186.713 275.692 189.252 282.919C191.792 290.146 195.307 296.884 199.799 303.134C204.096 309.384 209.37 314.853 215.62 319.54C221.87 324.032 228.608 326.864 235.834 328.036C228.608 329.208 221.967 332.04 215.913 336.532C209.858 340.829 204.584 346.103 200.092 352.353C195.405 358.603 191.792 365.341 189.252 372.567C186.713 379.599 185.444 386.532 185.444 393.368V517.587C185.444 550.79 168.842 567.392 135.639 567.392H108.686V600.497Z" fill="#F5024D" fill-opacity="0.5"/>
                </svg>

                        
                </div>




</div>
            </div>

        </div>
    </div>
    
    </> );
}
 
export default Hero;